import React from 'react';
import {
    List,
    Datagrid,
    TextField,
    NumberField,
    DateField,
    ReferenceField,
    ShowButton
} from 'react-admin';

const ReturnList = props => (
    <List {...props}>
        <Datagrid rowClick="show">
            <ReferenceField
                source="rentId"
                reference="rents"
                label="Rent ID"
                linkType="show"
            >
                <TextField source="rentId" />
            </ReferenceField>

            <DateField
                source="date"
                label="Return Date"
                options={{ format: 'YYYY-MM-dd' }}
            />

            <NumberField source="price" label="Price $" />
            <ShowButton />
        </Datagrid>
    </List>
);

export default ReturnList;
